import React, { useEffect, useState } from 'react'
import loadingGif from '../../loading.gif'
import logo from '../../logo-oscuro.png'

export default function LinkPrev({ img, title, description, url }) {

    return (
        <div class="card">
            <img src={img.length ? loadingGif : img || logo} class="card-img-top" alt={title} />
            <div class="card-body">
                <h5 class="card-title">{title}</h5>
                <p class="card-text">{description}</p>
                <a href={url} target='_blank' className="btn btn-primary">Visitar página</a>
            </div>
        </div>
    )

}
